import React from "react";


const deleteEventModal = (props) => {
    console.log(props.to_do_id);
    return(
    <>(<div id="deleteEventModal">
        <h2>Event</h2>
        <p id="eventText">{props.eventText}</p>
        <button id="deleteButton" onClick={props.deleteButton}>Complete</button>
        <button id="closeButton" onClick={props.closeButton }>Close</button>
            <button id="detailsButton" onClick={() => {
                window.location.href=("http://127.0.0.1:8000/dashboard/todo/" + props.to_do_id);
            }}>detials</button>
            
    </div>
        <div id="modalBackDrop"></div>
        </>
        );
}

export default deleteEventModal;