import React from "react";

const header = (props) => {

    return (
        <div id="header">
            <div id="monthDisplay">{ props.dateString}</div>
            <div>
                <button id="backButton" onClick={props.onBack}>back</button>
                <button id="nextButton" onClick={props.onNext}>Next</button>


            </div>


        </div>
        )
};

export default header;