import React, { Component } from "react";
import Day from "../../Components/DaySquare/DaySqaure";

export class TimeObject {
    day = 0;
    month = 0;
    year = 0;
    hours = 0;
    minutes = 0;
    seconds = 0;

    constructor(date_) {
        this.year = parseInt(date_.substring(0,4));
        this.month = parseInt(date_.substring(5,7));
        this.day = parseInt(date_.substring(8, 10));
        this.hours = parseInt(date_.substring(11, 13));
        this.minutes = parseInt(date_.substring(14, 16));
        this.seconds = parseInt(date_.substring(17, 19));
    }
    timeString = () => {
        return `${this.year}/${this.month}/${this.day}/${this.hours}/${this.minutes}/${this.seconds})`
    }
    static dateString = (date_) => {
        if (date_) {
            let year = parseInt(date_.substring(0, 4));
            let month = parseInt(date_.substring(5, 7));
            let day = parseInt(date_.substring(8, 10));
            return `${day}/${month}/${year}`;
        }
      
    }

    static compareTime = (date_,b) => {
        if (date_&&b) {
           
            const a = new Date(date_);
            const due = new Date(b);
            
            return a<=due;
        }
        return false;
        
}
}


class Calendar extends Component {
  
    componentDidMount = () => {
     
    }
    loadDate = () => {
        const weekdays = ["Sunday", "Monday", "Tuesday", "Wednesday",
            "Thursday", "Friday", "Saturday"];
        const dt = new Date();
        /*    let day = dt.getDate();
            let month = dt.getMonth();
            let year = dt.getFullYear();*/


        if (this.props.nav !== 0) {
            dt.setMonth(dt.getMonth() + this.props.nav);
        }


        const day = dt.getDate();
        const month = dt.getMonth();
        const year = dt.getFullYear();

        /*    console.log(day);
            console.log(month);
            console.log(year);*/
        const daysInMonth = new Date(year, month + 1 + this.props.nav, 0).getDate();

        const firstDayOfMonth = new Date(year, month + this.props.nav, 1);

        const dateString = firstDayOfMonth.toLocaleDateString("en-uk", {
            weekday: "long",
            year: "numeric",
            month: "numeric",
            day: "numeric",

        });



        const paddingDays = weekdays.indexOf(dateString.split(", ")[0]);


     
        const days = [];
        for (let i = 1; i <= paddingDays + daysInMonth; i++) {

            const dateString = `${i - paddingDays}/${month + 1}/${year}`;

            if (i > paddingDays) {

                days.push({
                    value: i - paddingDays,
                    event: this.props.eventForDate(dateString),
                    isCurrentDay: i - paddingDays === day && this.props.nav === 0,
                    date: dateString


                })

            } else {
                days.push({
                    value: "padding",
                    event: null,
                    isCurrentDay: false,
                    date: ""


                })

            }


        }

       
        return days;
    }


    render = () => {

        const daysDivs = this.loadDate().map((d, index) => {
            return <Day
                key={index}
                day={d}
                clicked={
                    () => this.props.DaySquareOnClick(d)
                }



            />;

        });
        return (
            <div id="calendar">
                {daysDivs}
            </div>
            
            );
    }
}

export default Calendar;
