import './App.css';
import React from "react";
import Calendar, { TimeObject } from "./Container/Calendar/Calendar";
import CalendarHeader from "./Components/CalendarHeader/CalendarHeader";
import Day from "./Components/DaySquare/DaySqaure";
import NewEventModal from "./Components/NewEventModal/NewEventModal";
import DeleteEventModal from "./Components/DeleteEventModal/DeleteEventModal";
import axios from "./axios-calendar";
import Loading from "./Components/loading/loading";
function getCsrf() {
    var inputElems = document.querySelectorAll('input');
    var csrfToken = '';
    for (let i = 0; i < inputElems.length; ++i) {
        if (inputElems[i].name === 'csrfmiddlewaretoken') {
            csrfToken = inputElems[i].value;
            break;
        }
    }
    return csrfToken;
};

class App extends Calendar {
    state = {
        nav: 0,
        clicked: null,
        events: localStorage.getItem("events") ?
            JSON.parse(localStorage.getItem("events")) : [],
        days: [],
        dateDisplay: "",
        oldNav: 0,
        oldEvents: localStorage.getItem("events") ?
            JSON.parse(localStorage.getItem("events")) : [],
        loading: true,
      

    }
    eventForDate = (date) => {


        return this.state.events.find(e => e.date === date);
    }

    DaySquareOnClick=(d)=>{
        if (d.value !== "padding") {
            this.setState({ clicked: d.date });
        }
        }

    loadDate = () => {
        const dt = new Date();
        /*    let day = dt.getDate();
            let month = dt.getMonth();
            let year = dt.getFullYear();*/


        if (this.state.nav !== 0) {
            dt.setMonth(dt.getMonth() + this.state.nav);
        }
        this.setState({
            dateDisplay: `${dt.toLocaleDateString("en-uk",
                {
                    month: "long"
                })} ${dt.getFullYear()}`
        });
    }
    componentDidMount = () => {
        this.loadDate();
      
        this.loadEvents();
      
      
    }

    loadEvents = () => {
        this.setState({ "loading": true });
        const data = new FormData();
        data.append("title", "lala");
        axios.get("calendar/calendar_test", data).then(
            response => {
                console.log(response.data.todos.map(value => {

                    const date_time = value.date_complete != null;
                    this.createNewEventWithTime(value.title,
                        TimeObject.dateString(value.date_due),
                        date_time,
                        TimeObject.compareTime(value.date_complete, value.date_due),
                        TimeObject.compareTime(value.date_due, Date.now()),
                        value.id,
                    )
                    TimeObject.dateString(value.date_complete);
                    return value.id;
                }));
                this.setState({ "loading": false });


            }
        ).catch(err => {
            console.log(err);
        });
    }
    createNewEvent = (title) => {

        console.log(this.state.clicked);
        this.createNewEventWithTime(title, this.state.clicked, null,null,null);
    }
    createNewEventWithTime = (title,date,c,ct,od,id) => {


        this.setState({
            events: [...this.state.events, {
                title: title,
                date: date,
                completed: c,
                completedOnTime: ct,
                overdue: od,
                to_do_id: id
            }],
            clicked: null,
           

        })
    }
    onNextButton = () => {
        this.setState({ nav: this.state.nav+1 });
        console.log("a");
      
       
    }
    onBackButton = () => {
        this.setState({ nav: this.state.nav-1});
  
    }

    componentDidUpdate = () => {
        if (this.state.nav !== this.state.oldNav) {
            this.loadDate();
            this.setState({ oldNav: this.state.nav });
        }
        if (this.state.events !== this.state.oldEvents) {
            this.loadDate();
            this.setState({ oldEvents: this.state.events });
        }
    }
    render = () => {
     /*   this.setState({ events: JSON.stringify(this.events) });
*/
        let calendar = <Loading />;
        if (!this.state.loading) {
            calendar = (<Calendar
                nav={this.state.nav}
                DaySquareOnClick={this.DaySquareOnClick}
                eventForDate={this.eventForDate}
                createNewEvent={this.createNewEventWithTime}
            />);
        }
      
     
        return (
            <>
                <div id="container">
                    <CalendarHeader dateString={this.state.dateDisplay}
                        onNext={this.onNextButton}
                        onBack={this.onBackButton}
                    />

                    <div id="weekdays">
                        <div>Sunday</div>
                        <div>Monday</div>
                        <div>Tuesday</div>
                        <div>Wednesday</div>
                        <div>Thursday</div>
                        <div>Friday</div>
                        <div>Saturday</div>

                    </div>

                    {calendar}
                   


                </div>

                {this.state.clicked && !this.eventForDate(this.state.clicked)

                    ? <NewEventModal
                        onSave={title => this.createNewEvent(title)
                            

                        

                        }
                        closeModal={() => {
                            this.setState({ clicked: null });
                           
                        }}
                    /> : this.state.clicked && this.eventForDate(this.state.clicked) ?
                        <DeleteEventModal
                            eventText={this.eventForDate(this.state.clicked).title}
                            to_do_id={this.eventForDate(this.state.clicked).to_do_id}
                        deleteButton={() => {
                            //const newEvent = this.state.events.filter(e => e.date !== this.state.clicked);

                            /*this.setState({

                                events: newEvent

                            });*/
                            const event_id = this.eventForDate(this.state.clicked).to_do_id;
                            const data = new FormData();
                            data.append("title", "complete");
                            data.append("csrfmiddlewaretoken", '{{csrf_token}}') 
                            axios.post("dashboard/todo/" + event_id + "/complete", data).then((response) => {
                             
                                console.log("completed")
                                
                                this.setState({ clicked: null });
                                this.loadEvents();
                            }).catch(err => {
                                console.log(err);
                                this.setState({ clicked: null });
                            })
                        
                        }}
                        closeButton={() => {

                            this.setState({ clicked: null });
                        }}

                    /> : null
                }

              

            </>);
        
    }

    
}
export default App;

