import React from "react";

const day = (props) => {
    let classes = "day";
    let innerText = props.day.value ;
    if (props.day.value === "padding") {
        classes += " padding";
        innerText = "";
    }
    if (props.day.isCurrentDay) {
        classes += " currentDay";
    }
  //  console.log(props);
    let event = null;
    if (props.day.event) {
        let className = "event";
        if (props.day.event.completed) {
            if (props.day.event.completedOnTime) {
                className += " completed";
            } else {
                className += " late";
            }

        } else {
            if (props.day.event.overdue) {
                className += " overdue";
            }
         
        }

        event = (<div className={className}>
            {props.day.event.title}
        </div>)
    }
    return (<div className={classes}
        onClick={props.day.clicked}
        onClick={props.clicked}

    > {innerText}
        {event}
       

    </div>);
}

export default day;